import React, { useState, useEffect } from 'react'
import { useLocation, useParams, Switch, Route, useHistory, withRouter } from "react-router-dom";
//
import BorrowMarket from './BorrowMarket'
//
import BorrowHeader from './BorrowHeader';
import BorrowMaker from './BorrowMaker'
import BorrowConfirm from './BorrowConfirm'

import { useContentContextState } from '~/Code/Components/content_context';

//
const Borrow = ({ injectedProvider, selectedProvider, web3Modal, loadWeb3Modal }) => {
// Use route param.
	let { underluing } = useParams();
//
	const { symbol } = useParams()
	const { setOnePoolDataState, setPoolDataState, setReserveTokensContext } = useContentContextState();
	const { assetData } = setPoolDataState
	//
	useEffect(() => {
		if(assetData?.length === setPoolDataState.assetsActiveLength && setOnePoolDataState.assetDataOneReserves?.symbol !== symbol){
			setOnePoolDataState.setSymbol(symbol)
		}
	}, [assetData] )
// .
let location = useLocation();    

//
	return (
		<>
			<div className='container'>
			<div className={``}>
				<div className={`dashboard active`} >
					<div className="container">
						<div className="dashboard__body row-flex flex-col">

			{ underluing &&
				<div className="col card mb-30">
					<BorrowHeader selectedProvider={selectedProvider} />
				</div>
			}

			<Switch location={location}>

				<Route exact path='/borrow/:symbol/:underluing' component={() => ( 
					<BorrowMaker 
						selectedProvider={selectedProvider} 
						web3Modal={web3Modal}
						loadWeb3Modal={loadWeb3Modal}
						injectedProvider={injectedProvider}
					/> )}
				/>
				
				<Route path='/borrow/:symbol/:underluing/:amount/:borrowType?' component={() => ( 
					<BorrowConfirm 
						web3Modal={web3Modal}
						loadWeb3Modal={loadWeb3Modal}
						injectedProvider={injectedProvider}
						selectedProvider={selectedProvider} 
					/> )} 
				/>
				
			</Switch>

				<BorrowMarket 
					injectedProvider={injectedProvider}
					selectedProvider={selectedProvider} 
				/>

						</div>
					</div>
				</div>
			</div>
			
			</div>
		</>
	)
}


export default React.memo(Borrow)



		{/* <!-- Header -->
		<header className="header">
			<!-- Header-top -->
			@@include("../blocks/modules/header-top/header-top-connected.html", {
			"activePageName": "DAI Reserve overview",
			"Markets": "",
			"Dashboard": "",
			"Deposit": "",
			"Borrow": "active",
			"Swap": "",
			"Stake": "",
			"Governance": ""
			})
			<!-- Header-bottom -->
			@@include("../blocks/modules/header-bottom/header-bottom-borrow.html")
			<div id="overlay-main" className="overlay"></div>
		</header>
		<!-- Main content --> */}