import React, { useState, useEffect } from 'react';
// import { useStaticPoolDataContext } from '~GRAPHQL/static_graph-pool-data-provider_formated';
import { useContentContextState } from '~/Code/Components/content_context';
//
import BigNumber from "bignumber.js";
import { valueToBigNumber } from '@aave/protocol-js';
let Web3 = require('web3');
//
const AvalibleToBorrow = ({ selectedProvider, avalibleLiqudity, priceInEth, decimals }) => {
// Context.
    // const { userSummaryData } = useStaticPoolDataContext();
	const { setUserAccountData } = useContentContextState();
	const { userAccountData } = setUserAccountData

    console.log('avalibleLiquzzczxczxdity', userAccountData?.totalCollateralETH.toString())
	console.log('userAccountData?.availableBorrowsETH.toString()', userAccountData?.availableBorrowsETH.toString())

//
    let maxUserAmountToBorrow = new BigNumber(maxUserAmountToBorrow)
	    maxUserAmountToBorrow = userAccountData &&
            valueToBigNumber(Web3.utils.fromWei(userAccountData?.availableBorrowsETH.toString(), 'ether') || 0).div(
            Web3.utils.fromWei(priceInEth, 'ether')
	    );
    console.log('maxUserAmountToBorrow11111', maxUserAmountToBorrow?.toString())
//
    let maxAmountToBorrow = BigNumber.max(
		BigNumber.min(
			+avalibleLiqudity, 
			maxUserAmountToBorrow
		),
		0 // Default
	);
	console.log('maxAmountToBorrow111', maxAmountToBorrow.toString())
//
	if (
		maxAmountToBorrow.gt(0) &&
		userAccountData.totalCollateralETH.toString() !== '0' &&
		maxUserAmountToBorrow.lt(valueToBigNumber(avalibleLiqudity).multipliedBy('1.01'))
		) {
			maxAmountToBorrow = maxAmountToBorrow.multipliedBy('0.99');
		}
	const formattedMaxAmountToBorrow = maxAmountToBorrow && Number(maxAmountToBorrow).toFixed(decimals);
//

//
    return (
        <> { 
			avalibleLiqudity && priceInEth && formattedMaxAmountToBorrow !== 'NaN'
				? formattedMaxAmountToBorrow.substring(0, 4)
				: '-' 
		} </>
    )

}


export default React.memo(AvalibleToBorrow)