import React, { useState, useEffect } from 'react';
import { useHistory, useParams, Link } from "react-router-dom";
// Router.
// .
import AaveRouteActions from '../poolData-helpers/AaveRouteActions'
import Icon from '../../base/icon/Icon'
import { useStaticPoolDataContext } from '~GRAPHQL/static_graph-pool-data-provider_formated';
import { useContentContextState } from '~/Code/Components/content_context';
//
import DepositHeader from '../Deposit/DepositHeader';
// web3.
let Web3 = require('web3');
import { valueToBigNumber } from '@aave/protocol-js';
import BigNumber from "bignumber.js";

import PlzConnectWalletMan from '../../base/failures/PlzConnectWalletMan'
import BalanceIsStatus from '../../base/failures/BalanceIsStatus'
import UsdPrice from '../../base/UsdPrice/UsdPrice';
import RedrawTimer from '~BASE/RedrawTimer/RedrawTimer'

//
const BorrowMaker = ({ selectedProvider, injectedProvider, walletBalance, web3Modal, loadWeb3Modal }) => {
// Route params.
	let { symbol } = useParams()
// Context.
    const { setUserAccountData, setUserAssetDataContext, setOnePoolDataState } = useContentContextState()
	const { userAssetData } = setUserAssetDataContext
	const { userAccountData } = setUserAccountData
	const { assetDataOneReserves } = setOnePoolDataState

	const { setIsMobileWidthVersion } = useStaticPoolDataContext()
	const { isMobile } = setIsMobileWidthVersion
// 
	let mathOptionWithDec = +assetDataOneReserves?.decimals === 18 ? 'ether' : +assetDataOneReserves?.decimals === 8 ? 'gwei' : 'mwei'
// Max amount to borrow.
	let maxUserAmountToBorrow = new BigNumber(maxUserAmountToBorrow)
	 	maxUserAmountToBorrow =  userAccountData && assetDataOneReserves 
			&& valueToBigNumber(Web3.utils.fromWei(userAccountData?.availableBorrowsETH.toString(), 'ether') || 0).div(
			// There add new chainlink prices, or else.
			Web3.utils.fromWei(assetDataOneReserves.priceOracle.toString(), 'ether')
	);

	console.log('borrowTypuseasdasdarAccountDataeborrowType', maxUserAmountToBorrow?.toString())

	let maxAmountToBorrow = assetDataOneReserves && BigNumber.max(
		BigNumber.min(
			+assetDataOneReserves?.availableLiquidity,
			maxUserAmountToBorrow
		),
		0 // Default
	);

	if (
		assetDataOneReserves &&
		maxAmountToBorrow.gt(0) &&
		userAccountData?.availableBorrowsETH.toString() !== '0' &&
		maxUserAmountToBorrow.lt(valueToBigNumber(+assetDataOneReserves?.availableLiquidity).multipliedBy('1.01'))
	) {
		maxAmountToBorrow = maxAmountToBorrow.multipliedBy('0.99');
	}

	const formattedMaxAmountToBorrow = assetDataOneReserves && Number(maxAmountToBorrow);
	
	const history = useHistory();	
	const handleInterestModeSubmit = (amount, borrowType) => {
		history.push(`${history.location.pathname}/${amount}/${borrowType}`);
	}

// State.
	const [amountBorrow, setUserAmoutBorrow] = useInput({ className: 'amount__input', type: 'number', walletBalance })
	
	const avaliableBorrowLiqudity = assetDataOneReserves 
		&& Web3.utils.fromWei(assetDataOneReserves.availableLiquidity.toString(), mathOptionWithDec)

	const [maxActionType, setMaxActionType] = useState()
	useEffect(() => {
		if(+avaliableBorrowLiqudity < +formattedMaxAmountToBorrow){
			setMaxActionType('avaliableBorrowLiqudity')
		} else {
			setMaxActionType('formattedMaxAmountToBorrow')
		}	
	}, [+avaliableBorrowLiqudity && +formattedMaxAmountToBorrow])
	
		
	function useInput({ className, type, walletBalance }) {
		const [value, setValue] = useState();
		const input = assetDataOneReserves && 
			< >
				<input 
					className={className} 
					value={value} 
					type={type}
					onChange={e => 
						maxActionType === 'formattedMaxAmountToBorrow'
						? +e.target.value > formattedMaxAmountToBorrow
							? setValue(formattedMaxAmountToBorrow) 
							: setValue(e.target.value)
						: +e.target.value > avaliableBorrowLiqudity
							? setValue(avaliableBorrowLiqudity) 
							: setValue(e.target.value)
					}
				/>
				<button 
					className='amount__btn' 
					type="button"
					onClick={e => { 
						setValue((+avaliableBorrowLiqudity < +formattedMaxAmountToBorrow) 
							? (avaliableBorrowLiqudity)
							: (formattedMaxAmountToBorrow)
						)
					}} 
				>
					Max
				</button>
			</>
		return [value, input];
	}


	// UI error ivent
	const [inputErorr, setInputError] = useState()
	const [aprSelectError, setAprSelectError] = useState()
    useEffect(() => {
        if(inputErorr === true || aprSelectError === true){
            const timer = setTimeout(() => {
                setInputError(false);
				setAprSelectError(false);
            }, 400);
                return () => clearTimeout(timer);
        }   
    }, [inputErorr, aprSelectError])

	const [borrowType, setBorrowType] = useState()
	const selectActiveBorrowType = +userAssetData[symbol]?.currentVariableDebt > 0
		? 'Variable'
		: +userAssetData[symbol]?.currentStableDebt > 0
			? 'Stable'
			: 'None'

	useEffect(() => {
		if(borrowType !== selectActiveBorrowType){
			setBorrowType(selectActiveBorrowType)
		}
	}, [assetDataOneReserves])

	

	let RAY = 10**27 // 10 to the power 27
	const VariableButton = () => {
		return (
			<button
				onClick={() => {
					setBorrowType('Variable')
				}}
				className={`btn-b btn-b--borrow btn-b-borrow
					${borrowType === 'Variable' ? 'selectedBorrowType' : '' }
				`}
			>
				<div className="btn-b-borrow__icon">
					<p className="label label--md label--dark">Variable APR</p>
				</div>
				<div className="value value--blue">
					{(Number(assetDataOneReserves?.variableBorrowRate) / RAY) === 0
						? '0 %'
						: (Number(assetDataOneReserves?.variableBorrowRate) / RAY * 100).toFixed(4) + ' %'
					}
				</div>
			</button>
		)
	}
	
	const StableButton = () => (
		<button 
			onClick={() => setBorrowType('Stable')} 
			className={`btn-b btn-b--borrow 
				${borrowType === 'Stable' ? 'selectedBorrowType' : '' }
			`}
		>
			<div className="btn-b-borrow__icon">
				<p className="label label--md label--dark">Stable APR</p>
			</div>
			<div className="value value--red">
				{ (Number(assetDataOneReserves.stableBorrowRate) / RAY) === 0
					? '0 %'
					: (Number(assetDataOneReserves.stableBorrowRate) / RAY * 100).toFixed(4) + ' %'
				}</div>
		</button>
	)
	

	const [isLoad, setIsLoad] = useState(false)

	useEffect(() => {
		setIsLoad(true)
	}, [])


	useEffect(() => {
		window.scroll(0, 290);
	} ,[]) 


console.log('maxAmountToBorrowasdasda', assetDataOneReserves?.availableLiquidity.toString())


	//
    return (
		injectedProvider 
		&& assetDataOneReserves 
		&& maxAmountToBorrow 
		&& +maxAmountToBorrow !== 0 
		&& !isNaN(maxAmountToBorrow)
			?
        <>
        <div className={`${ isLoad && assetDataOneReserves ? 'active_block' : 'hidden_block' }`}>

			<div className="col card grow-1">

				<AaveRouteActions type={'back'} classNameChild={"btn btn--red btn--small"} />
			
				<RedrawTimer />
				
					<div className={`${ isLoad && assetDataOneReserves ? 'ahashakeheartache' : '' } card__content card__content--md p-60-24 m-auto`}>
						<Icon 
							showImg={true} 
							showSpan={false} 
							symbolChild={symbol} 
							directory={'/assets-main'} 
						/> 
						<div className="detail__top mb-40 text-center">
							<div className='df-center mb-20'>
								
								<h2 className="title mr-8">How much would you like to borrow?</h2>

							</div>
							
							<p className="description">
								Please enter an amount you would like to borrow. The maximum amount you can borrow is shown below.
							</p>
						</div>

						<div className="detail__body">

							<div className={` ${isMobile ? 'mobile-detail-body' : 'grid-row grid-row--mb-20 mt-50' }   `}>

								{!isMobile &&
									<div className="grid-row__left flex-col">
										<div className="label label--md">
											Available to borrow
										</div>
									</div>
								}
								
	
								<div className="grid-row__right">
									<div className='flex-col'>
									{isMobile &&
										<div className="label label--md">
											Available to borrow
										</div>
									}
										<div className="label">
											<div className="value">{ !isNaN(formattedMaxAmountToBorrow) ?
												+formattedMaxAmountToBorrow > +avaliableBorrowLiqudity 
													? (avaliableBorrowLiqudity + ' ' + symbol) 
													: (formattedMaxAmountToBorrow + ' ' + symbol)
											: '0'}</div>
										</div>
										
										<div className="label">
											{ assetDataOneReserves && userAccountData && formattedMaxAmountToBorrow !== NaN && avaliableBorrowLiqudity &&
												<UsdPrice
													tableBool={true} 
													offUsdSymbols={false} 
													valueCalculate={assetDataOneReserves?.priceOracle.toString()} 
													decimals={+assetDataOneReserves?.decimals}  
													selectedProvider={selectedProvider} 
													currentBalanceForUserTokenChildInWei={formattedMaxAmountToBorrow ?
														+formattedMaxAmountToBorrow > +avaliableBorrowLiqudity 
															? avaliableBorrowLiqudity 
															: formattedMaxAmountToBorrow
												: ''
											}
										/>}
										</div>
									</div>
								</div>

							</div>


							<div className="grid-row__base">

								<div className={` ${inputErorr ? `ahashakeheartache` : ''} amount`}>
									{setUserAmoutBorrow}
								</div>
								
									{ assetDataOneReserves && amountBorrow &&
										<UsdPrice
											classNameChild={'posa'}
											tableBool={true} 
											offUsdSymbols={false} 
											valueCalculate={assetDataOneReserves?.priceOracle.toString()} 
											decimals={+assetDataOneReserves?.decimals}  
											selectedProvider={selectedProvider} 
											currentBalanceForUserTokenChildInWei={
												amountBorrow ? amountBorrow : 0
											}
										/>
									}

							</div>

							<div className={` ${aprSelectError ? `ahashakeheartache` : ''} detail__body`}>
								<div className="buttons-wrapper buttons-wrapper--borrow">
									{assetDataOneReserves.stableBorrowRateEnabled
										? <>
											<VariableButton />
											{selectActiveBorrowType !== 'none' || selectActiveBorrowType !== 'Variable' && <StableButton />}
										  </>
										: <>
											<VariableButton />
										  </>
									}
								</div>
							</div>

							<div className="grid-row mt-20">
								<div className="grid-row__base text-center">
									
									<button className='btn btn--blue btn--big m-auto grid-row__base text-center' 
										onClick={ () => {
											borrowType === 'Stable' ||
											borrowType === 'Variable' && 
											typeof(amountBorrow) !== 'undefined' 
												? handleInterestModeSubmit(amountBorrow, borrowType)
												: setInputError(true), setAprSelectError(true);
											} 
										}
									>
										Continue
									</button>

								</div>
							</div> 

						</div>
						</div>			

					
				</div>
			</div>
    	</>
		: !injectedProvider
			? <PlzConnectWalletMan loadWeb3Modal={loadWeb3Modal} web3Modal={web3Modal} /> 
			: <BalanceIsStatus 
				selectedProvider={selectedProvider} 
				web3Modal={web3Modal} 
				walletBalanceInHumanityDecimals={maxAmountToBorrow?.toString() === '0' || isNaN(maxAmountToBorrow) ? 0 : undefined} 
				symbol={symbol}  
				action={'borrow'}
				loadWeb3Modal={loadWeb3Modal} 
			/>
    )
}



export default BorrowMaker





