import React, { useEffect, useMemo, useState, useCallback } from 'react'
// 
import { useTable, useSortBy } from "react-table";
// Components.
// import { useAaveData } from "../poolData-helpers/AaveData"
import { useStaticPoolDataContext } from '~GRAPHQL/static_graph-pool-data-provider_formated';
import { useContentContextState } from '~/Code/Components/content_context';

import AaveRouteActions from "../poolData-helpers/AaveRouteActions"
import AvalibleToBorrow from "./AvalibleToBorrow/AvalibleToBorrow"
// WEb3.
import Icon from '../../base/icon/Icon';
import Loader from '~BASE/loader/loader'

import UsdPrice from '~BASE/UsdPrice/UsdPrice';
import BigNumber from "bignumber.js";
import { valueToBigNumber } from '@aave/protocol-js';
//
let Web3 = require('web3');
//
const BorrowMarket = ({ selectedProvider, injectedProvider }) => {
//
    const { setIsMobileWidthVersion } = useStaticPoolDataContext()
    const { isMobile } = setIsMobileWidthVersion

    const { setPoolDataState, setUserAccountData,  setReserveTokensContext } = useContentContextState();
    const { assetData } = setPoolDataState
    const { reserveTokens } = setReserveTokensContext
    const { userAccountData } = setUserAccountData

    // console.log('aaa123123', userAccountData);
    // let getDat = newAssetData.map( (value) => console.log(value) )

    const [balance, setBalance] = useState();
    const [showActiveAssets, setShowActiveAssets] = useState(false);
    const [displayCurrency, setDisplayCurrency] = useState('native');
//
    let ethPrice = '4632.22';
    let convertNative = ['USD', 'ETH'].includes(displayCurrency);
    let showUsdPrice = (ethPrice && displayCurrency === 'USD');

//

    const columns2 = useMemo(
        () => [
    
          {
            Header: ' ',
            accessor: value => <div style={{position: 'absolute', right: '55%', top: '50%'}}> <Loader /> </div>,
          },
          
        ]
    )

//
    const columns = useMemo(
        () => [
            {
                Header: 'Token',
                accessor: value => <Icon showImg={true} showSpan={true} symbolChild={value.symbol} directory={'/assets-main'} />,
            },
            {
                Header: 'Avaliable Liquidity',
                accessor: value => {
                    let ethToWeiProp = +value.decimals === 18 ? 'ether'
                        : +value.decimals === 6 
                            ? 'mwei'
                            : 'gwei'

                    let toFixedNumber = +value.decimals === 18 ? 6
                        : +value.decimals === 6 
                            ? 6
                            : 8

                    let renderAvaliableLiqudity = Number(value.availableLiquidity) === 0
                        ? 0
                        : Number(Web3.utils.fromWei(value.availableLiquidity.toString(), ethToWeiProp)).toFixed(toFixedNumber)
                    
                    return (
                        isMobile
                            ? <div style={{ width: '100%', justifyContent: 'space-between', display: 'flex' }}>
                                    <span>Avaliable liquidity </span>
                                    <span>{ renderAvaliableLiqudity }</span>
                                </div>
                            : renderAvaliableLiqudity
                    )
                }
            },
            {
                Header: 'Max to borrow',
                accessor: value => {

                    let ethToWeiProp = +value.decimals === 18 ? 'ether'
                        : +value.decimals === 6 
                            ? 'mwei'
                            : 'gwei'

                    let maxUserAmountToBorrow = new BigNumber(maxUserAmountToBorrow)
                        maxUserAmountToBorrow = userAccountData && value 
                            && valueToBigNumber(Web3.utils.fromWei(userAccountData?.availableBorrowsETH.toString(), 'ether') || 0).div(
                            // There add new chainlink prices, or else.
                                Web3.utils.fromWei(value.priceOracle.toString(), 'ether')
                    );

                    let maxAmountToBorrow = BigNumber.max(
                        BigNumber.min(
                            +value?.availableLiquidity,
                            maxUserAmountToBorrow
                        ),
                        0 // Default
                    );

                    if (
                        maxAmountToBorrow.gt(0) &&
                        userAccountData?.availableBorrowsETH.toString() !== '0' &&
                        maxUserAmountToBorrow.lt(valueToBigNumber(+value?.availableLiquidity).multipliedBy('1.01'))
                    ) {
                        maxAmountToBorrow = maxAmountToBorrow.multipliedBy('0.99');
                    }
                    const formattedMaxAmountToBorrow = Number(maxAmountToBorrow.toString()).toFixed(2);

                    return (
                        <> {isMobile ?
                                <div style={{ width: '100%', justifyContent: 'space-between', display: 'flex' }}>
                                    <span>Max to borrow</span>
                                    <span>{(isNaN(formattedMaxAmountToBorrow) || !injectedProvider) ? '-' : formattedMaxAmountToBorrow}</span>
                                </div>
                                : <span>{(isNaN(formattedMaxAmountToBorrow) || !injectedProvider) ? '-' : formattedMaxAmountToBorrow}</span>
                            }
                        </>
                    )
                }
            },
            {
                Header: 'Stable APR',
                accessor: value => {
                    
                
                    let RAY = 10**27 // 10 to the power 27
                    let stableBorrowAPR = (Number(value.stableBorrowRate) / RAY) === 0 
                        ? '0' 
                        : (Number(value.stableBorrowRate) / RAY * 100).toFixed(4)

                    
                    return (
                        <>
                            {isMobile 
                                ? <div style={{ width: '100%', justifyContent: 'space-between', display: 'flex' }}>
                                    <span>Stable APR</span>
                                    <span>{stableBorrowAPR + ' %'}</span>
                                  </div>
                                : <span>{stableBorrowAPR + ' %'}</span>
                            }
                        </>
                    )
                }
            },
            
            {
                Header: 'Variable APR',
                accessor: value => {

                    let RAY = 10**27 // 10 to the power 27.
                    let variableBorrowAPR = (Number(value.variableBorrowRate) / RAY) === 0
                        ? '0'
                        : (Number(value.variableBorrowRate) / RAY * 100).toFixed(4)

                    return (
                        <>
                            {isMobile ?
                                <div style={{ width: '100%', justifyContent: 'space-between', display: 'flex' }}>
                                    <span>Variable APR</span>
                                    <span>{variableBorrowAPR + ' %'}</span>
                                </div>
                                : <span>{variableBorrowAPR + ' %'}</span>
                            }
                        </>
                    )
                } 
            },
            
            {
                Header: 'Actions',
                accessor: value => {
                    return ( 
                        <div className='walletBal'>
                            {isMobile ? 'Actions' : ' '}
                            <AaveRouteActions 
                                type='borrow' 
                                classNameChild={"btn btn--blue btn--small"} 
                                assetData={value} 
                            />
                        </div> 
                    )
                }
            },
        ]
    )

const Table = useCallback(({ columns, data, filterable }) => {
// Use the state and functions returned from useTable to build your UI
    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
    } = useTable({ columns, data },
        useSortBy       
    )

    const [isLoad, setLoaded] = useState()

    useEffect(() => {
        setLoaded(true)
    }, [])

    return (
        <div className={`${ isLoad ? 'active_block' : 'hidden_block' }`}>


            <div className='table__content table__content--classic table-offset'>
                <div className='scroll-content'>
                    <table className='table' {...getTableProps()} >
                        {!isMobile ? 
                            <thead>
                                {headerGroups.map(headerGroup => (
                                    <tr className='table-header__body --dark' {...headerGroup.getHeaderGroupProps()}>
                                        {headerGroup.headers.map(column => (
                                            <th className='table-header__col --dark' 
                                                {...column.getHeaderProps(column.getSortByToggleProps())}
                                            >
                                                {column.render('Header')}
                                                <span>
                                                {column.isSorted
                                                    ? column.isSortedDesc
                                                        ? ' 🔽'
                                                        : ' 🔼'
                                                    : ''}
                                                </span>
                                            </th>
                                        ))}
                                    </tr>
                                ))}
                            </thead>
                            : '' }
                        <tbody {...getTableBodyProps()}>
                            {rows.map(row => {
                                prepareRow(row)
                                return (
                                    <tr style={{ width: '100%' }} className='table-item table-value table-value--dark mb-8' {...row.getRowProps()}>
                                        {row.cells.map(cell => {
                                            return (
                                            <td 
                                                className='dataTables'
                                                {...cell.getCellProps()}
                                            >
                                                {cell.render('Cell')}
                                            </td>
                                            )
                                        })}
                                    </tr>
                                )
                            })}
                        </tbody>

                    </table>
                </div>
            </div>
        </div>
    )
    }, []
)



    return (
        <>

            <Table
                data={
                    assetData?.length === 0 && reserveTokens
                    ? reserveTokens 
                    : assetData
                }
                columns={assetData.length === 0 ? columns2 : columns} 
            />
          
        </>
    )

}




export default React.memo(BorrowMarket)
