import React, { useState, useEffect } from 'react';
// route.
import { useLocation, useParams, Switch, Route, useHistory, withRouter } from "react-router-dom";
// aave action buttons.
import AaveAction from '../poolData-helpers/AaveAction';
import AaveRouteActions from '../poolData-helpers/AaveRouteActions'
import { abi as IErc20 } from '~ABIs/abis/erc20.json';
import Icon from '../../base/icon/Icon'
import { ethers } from "ethers";
// React context && data.
import UsdPrice from '~BASE/UsdPrice/UsdPrice';
import Loader from '~BASE/loader/loader'
import RedrawTimer from '~BASE/RedrawTimer/RedrawTimer'

import PlzConnectWalletMan from '../../base/failures/PlzConnectWalletMan'
import BalanceIsStatus from '../../base/failures/BalanceIsStatus'

// import { useAaveData } from "../poolData-helpers/AaveData"
import { useStaticPoolDataContext } from '~GRAPHQL/static_graph-pool-data-provider_formated';
import { useContentContextState } from '~/Code/Components/content_context';
//
import Transaction_status from '~BASE/Transaction_status/Transaction_status'

//
const BorrowConfirm = ({ selectedProvider, walletBalance, loadWeb3Modal, lendingPool, injectedProvider, web3Modal }) => {
// Context.

	const { actionTransactionDataState, setOnePoolDataState, contracts } = useContentContextState()
	const { transactionReceipt, transactionHash } = actionTransactionDataState

	const { assetDataOneReserves } = setOnePoolDataState

	const { setIsMobileWidthVersion } = useStaticPoolDataContext()
	const { isMobile } = setIsMobileWidthVersion
// Use route param.
	let { symbol, amount, underluing, borrowType } = useParams()
// Props params.
    let signer = selectedProvider?.getSigner()
	const AmountFromRoute = amount.replace(/[^0-9.,-]/g, '').toString()

    const { lendingPoolContract, LENDING_POOL_ADDRESS } = contracts
//
	const [loaded, setLoaded] = useState()
	const [allowanceState, setAllowance] = useState()
//
	const getAllowanceStatus = async () => {
		let tokenContract = new ethers.Contract(underluing, IErc20, signer);
		let address = await signer.getAddress()
		let _allowance = await tokenContract.allowance(address, LENDING_POOL_ADDRESS)
		setAllowance(_allowance)
	}
//	
	useEffect(() => {
		setLoaded(true)
		getAllowanceStatus()
	}, [selectedProvider])
	// let mathOptionWithDec = +assetDataOneReserves?.decimals === 18 ? 'ether' : +assetDataOneReserves?.decimals === 8 ? 'gwei' : 'mwei'	
//
	console.log('allowanceStateadasdas', allowanceState?.toString())
// .
	// Animation.
	const [isLoad, setIsLoad] = useState(false)
	useEffect(() => {
		setIsLoad(true)
	}, [])


	

    return (
		assetDataOneReserves && injectedProvider 
			?
        <div className={`${ isLoad ? 'active_block' : 'hidden_block' }`}>
			<div className={`${transactionReceipt.status === 1 ? 'borderGreen' : ''} col card grow-1`}>

					<AaveRouteActions type={'back'} classNameChild={"btn btn--red btn--small"}  />

					<RedrawTimer />

					<div className="card__content card__content--md p-60-24 m-auto">

						<Icon 
							showImg={true} 
							showSpan={false} 
							symbolChild={symbol} 
							directory={'/assets-main'} 
						/> 

						<Transaction_status 
							action={'Borrow'}
						/>

						<div className="detail__body">

							<div 
								style={{width: '100%'}}
								className={` ${isMobile ? '' : 'grid-row grid-row--mb-40 mt-30' }  `}
							>
								
								{!isMobile &&
									<div className="grid-row__left">
										<div className="label label--md flex-col">
											Amount
										</div>
									</div>
								}

								<div className="grid-row__right">

									
									<div 
										className="flex-col"
									>
										{isMobile && 'Amount'}
										<div className='value'>
											{ amount + ' ' + symbol }
										</div>
										<div className='text-right'>
											{ assetDataOneReserves && 
												<UsdPrice 
													tableBool={true} 
													offUsdSymbols={false} 
													valueCalculate={assetDataOneReserves?.priceOracle.toString()} 
													decimals={+assetDataOneReserves?.decimals}  
													selectedProvider={selectedProvider} 
													currentBalanceForUserTokenChildInWei={amount} 
												/>
											}
									 	</div>
									</div>
									
								</div>
								

								<div className="grid-row__left">
									{ !isMobile && 
										<div className="label label--md">
											Selected borrow type APR
										</div>
									}
									
								</div>
								<div className="grid-row__right">
									{ isMobile && 
										<div className="label label--md">
											Selected borrow type APR
										</div>
									}
									<div className="label value">
										{ borrowType }
									</div>
								</div>
							</div>

							<div className="grid-row grid-row--mb-40 mt-30">
								<div className="grid-row__base">
									<div className="progress">

										<ul className={`progress__steps 
												${ transactionReceipt.status === 1
														? 'step-3'
														: transactionHash?._transactionHash
															? 'step-2'
															: 'step-1'
												}`}>
													<li></li>
													<li></li>
													<li></li>
													<li></li>
													<li></li>
										</ul>

										<ul className="progress__labels">
											{/* {allowanceState && allowanceState.toString() !== '0' 
												? '' 
												: <li className="label--md label--dark">Approve</li>} */}
											
													<li className="label--md label--dark">Borrow</li>
													<li className="label--md label--dark">Finished</li>
										</ul>
									</div>
								</div>
							</div>

							

							<div className='grid-row'>
								<div className='grid-row__left'>
									{allowanceState && allowanceState.toString() === '0' 
										? null
										// ? <div className='grid-row__left'>
										// 	<div className="value mb-5">Approve</div>
										// 	<p className='label label--wrap'>Please approve before borrowing.</p>
										// 	</div>
										: transactionHash?._transactionHash
											? <div className='grid-row__left mb-5'>
												<p className='label label--wrap'>
													{ transactionReceipt.status === 1 
														? 'You transaction has succefull' 
														: transactionHash?._transactionHash
															? 'Wait you transaction has sent'
															: null
													}
												</p>
											</div>
											: null
									}
								</div>
								<div className='grid-row__right a-end'>
									{transactionHash?._transactionHash
										? <Loader /> 
										: transactionReceipt.status !== 1 &&
										<div className="grid-row__right a-end">
											<AaveAction 
												type='borrow'
												divWrapperClassNameChild='grid-row__right a-end' 
												buttonClassNameChild="btn btn--blue btn--small"
												assetData={assetDataOneReserves} 
												lendingPool={lendingPool} 
												signer={signer} 
												borrowType={borrowType === 'Stable' ? 1 : 2}
												amountFromRouteUrl={AmountFromRoute} 
												lendingPoolContract={lendingPoolContract} 
											/> 
										</div>
									}
								</div>
							</div>
						</div>

				</div>
 
			</div>
		</div>

		: !injectedProvider
			? <PlzConnectWalletMan loadWeb3Modal={loadWeb3Modal} web3Modal={web3Modal} /> 
			: <BalanceIsStatus 
				selectedProvider={selectedProvider} 
				web3Modal={web3Modal} 
				walletBalanceInHumanityDecimals={AmountFromRoute} 
				symbol={symbol}  
				action={'borrow'}
				loadWeb3Modal={loadWeb3Modal} 
			/>

    )

}



export default React.memo(BorrowConfirm)




